import React, { Component, useEffect, useState } from 'react';
// import { Helmet } from "react-helmet";
import DocumentMeta from 'react-document-meta';
import './contact-us.css';
import PageBanner from '../../components/ui/page-banner/page-banner';
import SectionHeading from '../../components/pages-component/section-heading/section-heading';
import ContactUsForm from './components/contact-us-form/contact-us-form';
import Modal from '../../components/ui/modal/modal';
import FormSuccessMsg from '../../components/pages-component/form-success-msg/form-success-msg';
import postGeneralQuery from '../../services/api/post-general-query';
import postMediaQuery from '../../services/api/post-media-query';
import postDistributorQuery from '../../services/api/post-distributor-query';
import getQueryReport from '../../services/api/get-queries-report'
import * as utilityFunctions from '../../utility-functions/utility-functions';
import { connect } from 'react-redux';
import Header from '../../components/layout/header/header';
import Footer from '../../components/layout/footer/footer';
import IconsubHeading from '../../components/pages-component/icon-subheading/icon-subheading';

const ContactUs = (props) => {

  const [ formData, setformData ] = useState(new FormData());
  const [ natureOfQuery, setnatureOfQuery ] = useState(null);
  const [ isStateRequired, setisStateRequired ] = useState(false);
  const [ allowStateField, setallowStateField ] = useState(false);

  const [ formValid, setformValid ] = useState(true);
  const [ errorMsg, seterrorMsg ] = useState(null);
  const [ formSubmissionStart, setformSubmissionStart ] = useState(false);
  const [ showModal, setshowModal ] = useState(false);

  useEffect(() => {
    window.scrollTo(0, 0);
  },[])

  const onClearningFormData = () => {
    props.formData.delete('name');
    props.formData.delete('email');
    props.formData.delete('contact_number');
    props.formData.delete('message');
    props.formData.delete('region');
    props.formData.delete('state');
  }

  const resetProperties = () => {
    props.natureOfQuery(null);
    props.StateRequired(false);
    props.allowStateField(false);
  }

  const onSubmitCallBack = (data) => {
    if (data.status === 201) {
      document.querySelector(".form--contact-us-1").reset();
      onClearningFormData();
      resetProperties();
      utilityFunctions.clearSelectField('.select-input');
      setformValid(true)
      seterrorMsg(null)
      setformSubmissionStart(false)
      setshowModal(true)
    } else {
      if (data.response) {
        // console.log(data.response.data);
        setformValid(false)
        seterrorMsg(data.response.data)
        setformSubmissionStart(false)
        // console.log(props.natureOfQuery);
      } else {
        const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
        setformValid(false)
        seterrorMsg(genericErrorMsg)
        setformSubmissionStart(false)
      }
    }
  }

  const onSubmitHandler = (event) => {
    event.preventDefault();
    setformSubmissionStart(true)

    switch (props.natureOfQuery) {
      case 'General Queries':
        postGeneralQuery(onSubmitCallBack, props.formData);
        break;
      case 'Become a reseller or distributor':
        postDistributorQuery(onSubmitCallBack, props.formData);
        break;
      case 'Media Queries':
        postMediaQuery(onSubmitCallBack, props.formData);
        break;

      default:
        alert('Something is wrong. Please try again.');
        setformSubmissionStart(false)
        break;
    }
  }

  const onInputChange = (event) => {
    const inputFieldName = event.target.name;
    const targetValue = event.target.value;
    const targetType = event.target.type;
    switch (targetType) {
      case 'text':
        if (inputFieldName === 'name') {
          props.formData.delete('name');
          props.formData.append('name', targetValue);
        }
        else
          if (inputFieldName === 'contact_number') {
            const contactNo = targetValue;
            props.formData.delete('contact_number');
            props.formData.append('contact_number', contactNo);
          }
        break;

      case 'email':
        if (inputFieldName === 'email') {
          props.formData.delete('email');
          props.formData.append('email', targetValue);
        }
        break;

      case 'select-one':
        if (inputFieldName === 'natureOfQuery') {
          props.natureOfQuery = targetValue;
          if (targetValue === 'General Queries') {
            props.aallowStateField(true);
          } else {
            props.allowStateField(false);
            props.isStateRequired(false);
          }
          // console.log('allowStateField', allowStateField, 'isStateRequired', isStateRequired);
        } else if (inputFieldName === 'region') {
          props.formData.append('region', targetValue);
          if (targetValue.toLowerCase() === 'india' && props.allowStateField === true) {
            props.isStateRequired(true);
            // console.log('allowStateField', allowStateField, 'isStateRequired', isStateRequired);
          } else {
            props.isStateRequired(false)
            // console.log('allowStateField', allowStateField, 'isStateRequired', isStateRequired);
          }
        } else if (inputFieldName === 'state') {
          props.formData.append('state', targetValue);
        }
        break;

      case 'textarea':

        props.formData.delete('message');
        props.formData.append('message', targetValue);
        break;

      default:
        break;
    }

    props.setformData(props.formData)

  }

  const closeModal = () => {
    setshowModal(false)
  }

  const queryReport = () => {
    getQueryReport()
  }

  const meta = {
    title: 'Contact us | Ethereal Machines-Bangalore, India.',
    meta: {
      property: {
        'title': 'Contact us | Ethereal Machines-Bangalore, India.',
        'description': ' Ethereal Machines – get in touch with us.',
      }
    }
  };

  return (
    <React.Fragment>
      <Header />
      <Modal show={showModal} clicked={closeModal}>
        <FormSuccessMsg />
      </Modal>
      <div className="page page--contact-us">
        {/* <Helmet>
        <title>Contact us | Ethereal Machines-Bangalore, India.</title>
        <meta name="description" content="Ethereal Machines – get in touch with us."/>
        </Helmet> */}

        <DocumentMeta {...meta} />
        <PageBanner heading={"Get in touch with Ethereal Machines"} subHeading={"Let's talk."} classValue={"page-banner--contact-us"} />
        <section className="section section--contact-us-form">
          <div className="contactinfo-container">

            <div className="contactinfo-new2">
              <SectionHeading name={"TALK TO US"} classValue={"  u-text-center"} />
              <IconsubHeading classValue={""} hasSubHeading subHeading={"+91 8792757504"} />
              <IconsubHeading classValue={""} hasSubHeading subHeading={"info@etherealmachines.com"} hasSubHeading1 subHeading1={"sales@etherealmachines.com"} />
            </div>

            <div className="contactinfo-quick-enquiry">
              <SectionHeading name={"Quick Enquiry form"} classValue={"u-margin-bottom-big-1 u-text-center"} hasSubHeading subHeading={"Please fill your details to get in touch with us."} />

              <ContactUsForm
                onInputChange={onInputChange}
                onSubmitHandler={onSubmitHandler}
                formSubmissionStart={formSubmissionStart}
                errorMsg={errorMsg}
                natureOfQuery={props.natureOfQuery}
                isStateRequired={props.isStateRequired}
                showLoader={formSubmissionStart}
              />

              <div className="container">
                {
                  props.session.session
                    ?
                    <div>
                      <button className="form-btn" onClick={queryReport}>Query Report</button>
                    </div>

                    :
                    null
                }
              </div>
            </div>

          </div>

        </section>
      </div>
      <Footer />
    </React.Fragment>
  );
}

const mapStateToProps = (state) => {
  return {
    session: state.login
  }
}

export default connect(mapStateToProps)(ContactUs);